@layer quanity-rocker {
  quantity-rocker {
    --border-color: #e1e1e1;
    font-size: inherit;
    width: fit-content;
    display: block;
  }

  quantity-rocker::part(wrapper) {
    width: inherit;
    font-size: inherit;
    font-family: inherit;
    font-weight: inherit;
    display: inline-flex;
  }

  quantity-rocker::part(decrement), quantity-rocker::part(increment), quantity-rocker::part(input) {
    background-color: unset;
    color: currentColor;
    border: 1px solid var(--border-color);
    font-size: inherit;
    font-family: inherit;
    font-weight: inherit;
    line-height: inherit;
    border-radius: 0;
    padding: 2px 7px;
  }

  quantity-rocker::part(decrement), quantity-rocker::part(increment) {
    cursor: pointer;
  }

  quantity-rocker::part(input) {
    width: inherit;
    text-align: center;
    appearance: textfield;
    border-inline: none;
  }
}
